.header {  
  &__nav {
    position: fixed;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    top: 0;
    left: 0;
    right: 0;
    text-align: center;
    height: $header-height;
    background-color: rgba(0, 0, 0, 0.75);
    color: #fff;
  }

  &__link {
    display: inline-block;
    font-size: 14px;
    margin: 0;
    padding: 20px 10px;
    color: #fff;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 1px;
    transition: all 0.3s;

    &:hover {
      box-shadow: 3px -5px 5px #000, -3px -5px 5px #000;
    }

    @include onDesktop {
      font-size: 20px;
    }
  }

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__item {
    display: inline-block;
    cursor: pointer;
  }

  &__img {
    background-repeat: no-repeat;
    background-size: contain;
    width: 25px;
    height: 25px;
    padding: 0 5px;
    margin-right: 10px;
    border-radius: 2px;
    cursor: pointer;
    transition: all 0.3s;

    &:hover {
      transform: perspective(100px) translateZ(10px);
    }

    &:active {
      transform: perspective(100px) translateZ(-10px);
    }

    &--login {
      background-image: url(images/login_icon.png);
    }

    &--logout {
      background-image: url(images/logout_icon.png);
    }
  }
}