body {
  margin: 0;
  background-color: #f5f6f1;
  font-family: "Roboto", Helvetica, sans-serif;
}
@media (min-width: 744px) {
  body {
    margin-top: 85px;
  }
}

body {
  margin-top: 80px;
}

.news {
  background-color: #fff;
  border: solid 1px #ced2ed;
  border-radius: 8px;
  box-sizing: border-box;
  padding: 8px;
  width: 90%;
  display: flex;
  align-content: center;
  justify-content: space-between;
  flex-direction: column;
  margin: 0 auto 25px;
}
@media (min-width: 744px) {
  .news {
    width: 80%;
  }
}
.news__isNotAuth {
  height: 8px;
}
.news__add-news {
  position: fixed;
  right: 20px;
  bottom: 20px;
  width: 50px;
  height: 50px;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url(./images/add-list-icon.png);
  opacity: 0.5;
  cursor: pointer;
  transition: all 0.3s;
}
.news__add-news:hover {
  opacity: 1;
}
.news__buttons {
  width: 90%;
  margin: 0 auto 5px;
  display: flex;
  justify-content: flex-end;
}
.news__button-container {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  height: 30px;
  width: 30px;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s;
}
.news__button-container:hover {
  background-color: #f5f6f1;
  border: solid 1px #ced2ed;
}
.news__button-container:active {
  background-color: #ced2ed;
  border: solid 1px #ced2ed;
}
.news__button {
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-size: contain;
  width: 16px;
  height: 16px;
}
.news__button--remove {
  background-image: url(./images/remove-icon-16x16.png);
}
.news__button--edit {
  background-image: url(./images/edit-icon-16x16.png);
}
.news__img {
  margin: 0 auto 10px;
  border-radius: 8px;
  width: 90%;
}
.news__title {
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 5px;
}
@media (min-width: 744px) {
  .news__title {
    font-size: 30px;
  }
}
.news__link {
  text-decoration: none;
  color: #000;
}
.news__description {
  text-align: justify;
  font-size: 12px;
}
@media (min-width: 744px) {
  .news__description {
    font-size: 16px;
  }
}
.news__up {
  position: fixed;
  left: 20px;
  bottom: 20px;
  width: 50px;
  height: 50px;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url(images/up.png);
  opacity: 0.5;
  cursor: pointer;
  transition: all 0.3s;
}
.news__up:hover {
  opacity: 1;
}

.profile__container {
  padding: 80px 20px 20px;
}
@media (min-width: 744px) {
  .profile__container {
    padding: 110px 50px 50px;
  }
}
.profile__text {
  text-align: justify;
  margin: 20px 0;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.page {
  scroll-behavior: smooth;
}
.page__container {
  padding-top: 80px;
  background-color: #f5f6f1;
}

.header__nav {
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  top: 0;
  left: 0;
  right: 0;
  text-align: center;
  height: 60px;
  background-color: rgba(0, 0, 0, 0.75);
  color: #fff;
}
.header__link {
  display: inline-block;
  font-size: 14px;
  margin: 0;
  padding: 20px 10px;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: all 0.3s;
}
.header__link:hover {
  box-shadow: 3px -5px 5px #000, -3px -5px 5px #000;
}
@media (min-width: 744px) {
  .header__link {
    font-size: 20px;
  }
}
.header__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.header__item {
  display: inline-block;
  cursor: pointer;
}
.header__img {
  background-repeat: no-repeat;
  background-size: contain;
  width: 25px;
  height: 25px;
  padding: 0 5px;
  margin-right: 10px;
  border-radius: 2px;
  cursor: pointer;
  transition: all 0.3s;
}
.header__img:hover {
  transform: perspective(100px) translateZ(10px);
}
.header__img:active {
  transform: perspective(100px) translateZ(-10px);
}
.header__img--login {
  background-image: url(images/login_icon.png);
}
.header__img--logout {
  background-image: url(images/logout_icon.png);
}

.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: calc(100vh - 60px);
  background-color: rgba(0, 0, 0, 0.7);
  position: fixed;
  left: 0;
  bottom: 0;
}
.modal--message {
  transition: all 0.5s;
  transform: scale(0);
  height: 100vh;
  cursor: pointer;
}
.modal hr {
  border-top: 2px solid red;
  border-radius: 5px;
}
.modal__content {
  border: 3px solid #999;
  cursor: auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #fff;
  color: #000;
  border-radius: 5px;
  max-width: 90vw;
}
.modal__title {
  font-weight: 400;
  padding: 10px;
}
.modal__container-close {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 20px;
}
.modal__close {
  margin-bottom: 10px;
  cursor: pointer;
  background-image: url(images/close-cross-icon.png);
  background-repeat: no-repeat;
  background-size: contain;
  width: 16px;
  height: 16px;
}

.modal.active {
  transform: scale(1);
}

.form {
  background-color: #fff;
  border: solid 1px #ced2ed;
  border-radius: 8px;
  box-sizing: border-box;
  padding: 8px;
  width: 90%;
  display: flex;
  align-content: center;
  justify-content: space-between;
  flex-direction: column;
  margin: 0 auto 25px;
  cursor: auto;
}
@media (min-width: 744px) {
  .form {
    width: 80%;
  }
}
.form--create {
  padding: 15px;
  background-color: white;
}
@media (min-width: 744px) {
  .form--create {
    width: 50%;
  }
}
.form__input {
  box-sizing: border-box;
  margin: 0 auto 10px;
  display: block;
  width: 100%;
  height: calc(2.25rem + 2px);
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #bdbdbd;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.form__input::placeholder {
  color: #212529;
  opacity: 0.4;
}
.form__input:focus {
  color: #212529;
  background-color: #fff;
  border-color: #bdbdbd;
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgba(158, 158, 158, 0.25);
}
.form__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.form__legend {
  margin-bottom: 10px;
  color: #212529;
}
.form__close {
  padding: 10px;
  cursor: pointer;
  background-image: url(images/close-cross-icon.png);
  background-repeat: no-repeat;
  background-size: contain;
  width: 24px;
  height: 24px;
}
.form__button {
  display: inline-block;
  width: 100%;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  background-color: #eee;
  border: 1px solid #bdbdbd;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  border-radius: 0.25rem;
  transition: background-color 0.15s ease-in-out;
}
.form__button:hover {
  background-color: #bdbdbd;
}
.form__button:active {
  position: relative;
  top: 2px;
}

body {
  margin-top: 0;
}

.home {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 0 10px 20px;
  width: 100vw;
  height: 100vh;
  background-image: url(images/home-background.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 0, 0;
}
@media (min-width: 744px) {
  .home {
    padding: 0 30px 20px;
  }
}
.home__content {
  height: 30vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.home__title {
  font-weight: 300;
  letter-spacing: 2px;
  font-size: 20pt;
}
@media (min-width: 744px) {
  .home__title {
    font-size: 28pt;
  }
}
.home__sub-title {
  font-weight: 300;
  letter-spacing: 1px;
  font-size: 15pt;
}
@media (min-width: 744px) {
  .home__sub-title {
    font-size: 20pt;
  }
}
.home__box-button {
  max-width: 400px;
}
.home__box-buttons {
  max-width: 700px;
  display: flex;
  justify-content: space-between;
}
@media (min-width: 744px) {
  .home__box-buttons {
    justify-content: flex-start;
    gap: 30px;
  }
}
.home__link {
  width: 45%;
  text-decoration: none;
  text-transform: uppercase;
}
.home__link--one {
  width: 100%;
}

