body {
  margin-top: calc($header-height + 20px);
}
.news {
  background-color: #fff;
  border: solid 1px #ced2ed;
  border-radius: 8px;
  box-sizing: border-box;
  padding: 8px;
  width: 90%;
  display: flex;
  align-content: center;
  justify-content: space-between;
  flex-direction: column;
  margin: 0 auto 25px;

  @include onDesktop {
    width: 80%;
  }

  &__isNotAuth {
    height: 8px;
  }

  &__add-news {
    position: fixed;
    right: 20px;
    bottom: 20px;
    width: 50px;
    height: 50px;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url(./images/add-list-icon.png);
    opacity: 0.5;
    cursor: pointer;
    transition: all 0.3s;

    &:hover {
      opacity: 1;
    }
  }

  &__buttons {
    width: 90%;
    margin: 0 auto 5px;
    display: flex;
    justify-content: flex-end;
  }

  &__button-container {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    height: 30px;
    width: 30px;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.3s;

    &:hover {
      background-color: #f5f6f1;
      border: solid 1px #ced2ed;
    }

    &:active {
      background-color: #ced2ed;
      border: solid 1px #ced2ed;
    }
  }

  &__button {
    box-sizing: border-box;
    background-repeat: no-repeat;
    background-size: contain;
    width: 16px;
    height: 16px;
    
    &--remove {
      background-image: url(./images/remove-icon-16x16.png);
    }
    
    &--edit {
      background-image: url(./images/edit-icon-16x16.png);
    }
  }

  &__img {
    margin: 0 auto 10px;
    border-radius: 8px;
    width: 90%;
  }

  &__title {
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 5px;

    @include onDesktop {
      font-size: 30px;
    }
  }

  &__link {
    text-decoration: none;
    color: #000;
  }

  &__description {
    text-align: justify;
    font-size: 12px;

    @include onDesktop {
      font-size: 16px;
    }
  }

  &__up {
    position: fixed;
    left: 20px;
    bottom: 20px;
    width: 50px;
    height: 50px;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url(images/up.png);
    opacity: 0.5;
    cursor: pointer;
    transition: all 0.3s;

    &:hover {
      opacity: 1;
    }
  }
}