.form {
  background-color: #fff;
  border: solid 1px #ced2ed;
  border-radius: 8px;
  box-sizing: border-box;
  padding: 8px;
  width: 90%;
  display: flex;
  align-content: center;
  justify-content: space-between;
  flex-direction: column;
  margin: 0 auto 25px;
  cursor: auto;

  @include onDesktop {
    width: 80%;
  }
  
  &--create {
    padding: 15px;
    background-color: rgba($color: #fff, $alpha: 1.0);

    @include onDesktop {
      width: 50%;
    }
  }

  &__input {
    box-sizing: border-box;
    margin: 0 auto 10px;
    display: block;
    width: 100%;
    height: calc(2.25rem + 2px);
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #bdbdbd;
    border-radius: 0.25rem;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    &::placeholder {
      color: #212529;
      opacity: 0.4;
    }

    &:focus {
      color: #212529;
      background-color: #fff;
      border-color: #bdbdbd;
      outline: 0;
      box-shadow: 0 0 0 0.2rem rgba(158, 158, 158, 0.25);
    }
  }

  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  &__legend {
  margin-bottom: 10px;
  color: #212529;
  }

  &__close {
    padding: 10px;
    cursor: pointer;
    background-image: url(images/close-cross-icon.png);
    background-repeat: no-repeat;
    background-size: contain;
    width: 24px;
    height: 24px;
  }

  &__button {
    display: inline-block;
    width: 100%;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    background-color: #eee;
    border: 1px solid #bdbdbd;
    padding: .375rem .75rem;
    font-size: 1rem;
    border-radius: .25rem;
    transition: background-color .15s ease-in-out;
    
    &:hover {
      background-color: #bdbdbd;
    }

    &:active {
      position: relative;
      top: 2px;
    }
  }
}