.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: calc(100vh - $header-height);
  background-color: rgba(0, 0, 0, 0.7);
  position: fixed;
  left: 0;
  bottom: 0;

  &--message {
    transition: all 0.5s;
    transform: scale(0);
    height: 100vh;
    cursor: pointer;
  }
  
hr {
  border-top: 2px solid red;
  border-radius: 5px;
}

  &__content {
    border: 3px solid #999;
    cursor: auto;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #fff;
    color: #000;
    border-radius: 5px;
    max-width: 90vw;
  }

  &__title {
    font-weight: 400;
    padding: 10px;
  }

  &__container-close {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 20px;
  }

  &__close {
    margin-bottom: 10px;
    cursor: pointer;
    background-image: url(images/close-cross-icon.png);
    background-repeat: no-repeat;
    background-size: contain;
    width: 16px;
    height: 16px;
  }
}

.modal.active {
  transform: scale(1);
}