body {
  margin-top: 0;
}
.home {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 0 10px 20px;
  width: 100vw;
  height: 100vh;
  background-image: url(images/home-background.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 0, 0;

  @include onDesktop {
    padding: 0 30px 20px;
  }

  &__content {
    height: 30vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
    font-weight: 300;
    letter-spacing: 2px;
    font-size: 20pt;

    @include onDesktop {
    font-size: 28pt;
    }
  }

  &__sub-title {
    font-weight: 300;
    letter-spacing: 1px;
    font-size: 15pt;

    @include onDesktop {
    font-size: 20pt;
    }
  }

  &__box-button {
    max-width: 400px;
  }

  &__box-buttons {
    max-width: 700px;
    display: flex;
    justify-content: space-between;

    @include onDesktop {
      justify-content: flex-start;
      gap: 30px;
    }
  }

  &__link {
    width: 45%;
    text-decoration: none;
    text-transform: uppercase;

    &--one {
      width: 100%;
    }
  }
}